export default {
	data() {
		return {
			
		}
	},
	methods: {
		getUserInfo(res){
			let {userInfo,rawData} = res.detail
			if(rawData){
				// 授权成功,跳转回到个人中心页面
				uni.setStorage({
					key:"userInfo",
					data:rawData
				})
				uni.reLaunch({
					url:"/pages/personal/personal"
				})
			}else{
				console.log("授权失败")
			}
			console.log(res)
		}
	}
}
